export const getStoryMap = (key) => {
    switch(key){
        case 'recipe': return recipeMap;
        case 'photo': return photoMap;
        case 'reading': return readingMap;
        case 'novel': return novelMap;
        default: return {};
    }
};

export const iconClass = (key) => {
    switch(key){
        case 'home': return "fas fa-home";
        case 'enter': return "fas fa-arrow-circle-right fa-lg";
        case 'top': return "fas fa-arrow-circle-up";
        case 'list': return "fas fa-angle-right";

        case 'recipe': return "fas fa-code";
        case 'photo': return "fas fa-camera";
        case 'reading': return "fas fa-book";
        case 'novel': return "fas fa-feather";

        case 'frontend': return "fab fa-html5";
        case 'backend': return "fas fa-leaf";
        case 'cloud': return "fas fa-tractor";
        case 'devops': return "fas fa-tools";
        case 'platform': return "fas fa-palette";

        case 'landscape': return "fas fa-cat";
        case 'portrait': return "fas fa-portrait";
        case 'culture': return "fas fa-archway";
        case 'flower': return "fas fa-tree";

        default: return "fas fa-angry";
    }
};

